import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AppointmentSetupComponent } from './appointment-setup.component';
import { AppointmentCategoryComponent } from './appointment-category/appointment-category.component';

import { AppointmentSetupResolver } from './appointment-setup.resolver';

const routes: Routes = [
  { path: '', component: AppointmentSetupComponent,
    children: [
      {
        path         : 'category/:type/:id',
        component    : AppointmentCategoryComponent,
        resolve: {
          AppointmentSetupResolver
        }
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AppointmentSetupRoutingModule { }
