import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve, Router, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs';

import { AppointmentSetupService } from './appointment-setup.service';


@Injectable({
  providedIn: 'root'
})
export class AppointmentSetupResolver implements Resolve<boolean> {

  constructor(
    private router: Router,
    private appointmentSetupService: AppointmentSetupService
  ) { }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any> | Promise<any> | any
  {

    var data = this.appointmentSetupService.getAppointmentSetupMatDrawer();

    if(data == null){

      const parentUrl = state.url.split('/').slice(0, -3).join('/');

      console.log(parentUrl)

      // Navigate to parent url
      this.router.navigateByUrl(parentUrl);

    }


    return;

  }

}
