import { Component, OnInit, ViewEncapsulation, ChangeDetectorRef } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Subject } from 'rxjs';

import { MatDrawerToggleResult } from '@angular/material/sidenav';
import { AppointmentSetupComponent } from 'src/app/modules/admin/appointment/appointment-setup/appointment-setup.component';

import { AppointmentSetupService } from 'src/app/modules/admin/appointment/appointment-setup/appointment-setup.service';

import { ApiService } from 'src/app/core/api/api.service';

import { SessionService } from 'src/app/core/session/session.service';
import { SharedService } from 'src/app/shared/services/shared.service';
import { ModalsService } from 'src/app/modules/ui/modals/modals.service';

import * as _ from 'underscore';
import * as moment from 'moment';
import _moment from "moment-timezone";

@Component({
  selector: 'app-appointment-category',
  templateUrl: './appointment-category.component.html',
  styleUrls: ['./appointment-category.component.scss'],
  encapsulation  : ViewEncapsulation.None
})
export class AppointmentCategoryComponent implements OnInit {

  isCategorySubmitted:boolean = true;
  isEdit:boolean = false;
  isView:boolean = false;

  category:any = {};

  private _unsubscribeAll: Subject<any>;

  constructor(
    private _activatedRoute: ActivatedRoute,
    private _router: Router,
    private _changeDetectorRef: ChangeDetectorRef,
    private _appointmentSetupComponent: AppointmentSetupComponent,
    private appointmentSetupService: AppointmentSetupService,
    private apiService: ApiService,
    private sessionService:SessionService,
    private sharedService:SharedService,
    private modalsService: ModalsService
  ) {

    // Set the private defaults
    this._unsubscribeAll = new Subject();

   }

  closeDrawer(): Promise<MatDrawerToggleResult>{
    return this._appointmentSetupComponent.matDrawer.close();
  }

  goBack() {
    this._appointmentSetupComponent.matDrawer.close();
    this.appointmentSetupService.setAppointmentSetupMatDrawer(null)
    this._router.navigate(['.'], {relativeTo: this._activatedRoute.parent});
  }

  submitAddAppointmentCategoryForm(form) {

    this.isCategorySubmitted = false;

    let details = {
      "id": 0,
      "name": this.category.name,
      "description": this.category.description,
      "clientId": this.sessionService.clientId,
      "active": true,
      "lastModified": _moment().toISOString(),
      "modifiedBy": this.sessionService.userId
    }

    if(this.isEdit) {
      details.id = this.category.id
    }

    this.apiService.addAppointmentCategory(details).subscribe((res:any) => {

      this.isCategorySubmitted = true;

      if(res.value) {
        this.modalsService.showResponseModal(`Category ${this.isEdit ? 'Updated' : 'Added'} successfully`, 'success').subscribe((res:any) => {
          if(!this.isEdit){
            this.appointmentSetupService.setAppointmentSetupEntryRefresh(true)
          }
          this.goBack();
        })

      }
      else {
        this.modalsService.showResponseModal('Some error occurred', 'error')
      }

    }, error => {

      this.isCategorySubmitted = true;
      this.modalsService.showResponseModal('Network error', 'error')

    });

  }

  ngOnInit(): void {



    //Open the drawer
    this.appointmentSetupService.appointmentsetupdrawercast.subscribe(res => {

      if(res != null) {
        if(res.id != undefined && res.id == this._activatedRoute.params['value'].id){
          this._appointmentSetupComponent.matDrawer.open();
          if(this._activatedRoute.params['value'].type == 'edit'){
            this.isEdit = true
            this.isView = false;
            this.category = res.data
          }
          else if(this._activatedRoute.params['value'].type == 'new') {
            this.isEdit = false;
            this.isView = false;
            this.category = {}
          }
          else {
            this.isEdit = false
            this.isView = true;
            this.category = res.data
          }
        }
      }

      //Mark for check
      this._changeDetectorRef.markForCheck();

    })



  }

}
