import { Component, OnInit, ViewEncapsulation, ChangeDetectorRef, ViewChild } from '@angular/core';
import { ActivatedRoute, Router, NavigationEnd } from '@angular/router';

import { FormControl } from '@angular/forms';

import { Subscription, Subject, BehaviorSubject, Observable } from 'rxjs';
import { filter, map, startWith, debounceTime, takeUntil } from 'rxjs/operators';
import { MatDrawer } from '@angular/material/sidenav';

import { AppointmentCategoryService } from 'src/app/swagger/api/controllers/AppointmentCategory';

import { AppointmentSetupService } from './appointment-setup.service';
import { SessionService } from 'src/app/core/session/session.service';
import { SharedService } from 'src/app/shared/services/shared.service';
import { ModalsService } from 'src/app/modules/ui/modals/modals.service';

@Component({
  selector: 'appointment-setup',
  templateUrl: './appointment-setup.component.html',
  styleUrls: ['./appointment-setup.component.scss'],
  encapsulation  : ViewEncapsulation.None
})
export class AppointmentSetupComponent implements OnInit {

  isDataLoaded:boolean = false;
  isError:boolean = false;

  @ViewChild('matDrawer', {static: true})
  matDrawer: MatDrawer;

  drawerMode: 'over';
  private _unsubscribeAll: Subject<any>;

  _appointmentsList:any;
  fullAppointmentsList:any = [];

  searchData:FormControl;

  search:string = '';
  deleteSubscribe:Subscription;

  ItemStartIndex: any = 0;
  ItemEndIndex: any;
  totalItems: any;
  itemLimit: any = 5;

  allData: any;

  message: any;

  constructor(
    private _changeDetectorRef: ChangeDetectorRef,
    private _activatedRoute: ActivatedRoute,
    private _router: Router,
    private appointmentSetupService: AppointmentSetupService,
    private sharedService:SharedService,
    private appointmentCategoryService: AppointmentCategoryService,
    private sessionService: SessionService,
    private modalsService: ModalsService
  ) {

     // Set the private defaults
     this._unsubscribeAll = new Subject();

     this.message = null;

     this._appointmentsList = new BehaviorSubject(null);
     this.searchData = new FormControl('');

  }

  get appointments$(): Observable<any>{
    return this._appointmentsList.asObservable();
  }

  getIndexParams(event){

    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;

  }

  onBackdropClicked() {

    // Get the current activated route
    let route = this._activatedRoute;
    while ( route.firstChild )
    {
        route = route.firstChild;
    }

    // Go to the parent route
    this._router.navigate(['/appointment/setup'], {relativeTo: route});
    this.matDrawer.close();

    // Mark for check
    this._changeDetectorRef.markForCheck();

  }

  addAppointmentCategory(event) {

    if(event) {

      // Get the current activated route
      let route = this._activatedRoute;
      while ( route.firstChild )
      {
          route = route.firstChild;
      }

      var id = this.sharedService.guid();

      this.appointmentSetupService.setAppointmentSetupMatDrawer({id:id, data: null, isedit: false});
      this._router.navigate(['category/new', id], {relativeTo: route});

      //Mark for check
      this._changeDetectorRef.markForCheck();

    }

  }

  editAppointmentCategory(appointment) {

    // Get the current activated route
    let route = this._activatedRoute;
    while ( route.firstChild )
    {
        route = route.firstChild;
    }

    this.appointmentSetupService.setAppointmentSetupMatDrawer({id:appointment.id, data: appointment, isedit: true});
    this._router.navigate(['category/edit', appointment.id], {relativeTo: route});

    //Mark for check
    this._changeDetectorRef.markForCheck();

  }

  viewAppointmentCategory(appointment) {

    // Get the current activated route
    let route = this._activatedRoute;

    while ( route.firstChild )
    {
        route = route.firstChild;
    }

    this.appointmentSetupService.setAppointmentSetupMatDrawer({id:appointment.id, data: appointment, isedit: false});
    this._router.navigate(['category/view', appointment.id], {relativeTo: route});

    //Mark for check
    this._changeDetectorRef.markForCheck();

  }

  deleteAppointmentCategory(id){
    this.modalsService.showDeleteConfirmModal(id)
  }

  getAppointmentCategoryList() {

    let params = {
      UserId: this.sessionService.userId,
      ClientId: this.sessionService.clientId
    }

    this.appointmentCategoryService.getAppointmentCategoryList(params).subscribe((res:any) => {

      this.isDataLoaded = true

      // Hide the message
      this.message = null;

      if(res.value) {

        let newData = res.value.filter(item => {
          return item.active
        });

        this.fullAppointmentsList = newData;
        this.allData = newData;
        this._appointmentsList.next(newData);
      }
      else {

        this.isError = true;

        // Show the error message
        this.message = {
          appearance: 'outline',
          content   : res.errors,
          shake     : true,
          showIcon  : false,
          type      : 'error'
        };

      }

    }, error => {

      this.isDataLoaded = true
      this.isError = true;

      // Show the error message
      this.message = {
        appearance: 'outline',
        content   : 'Some error occurred',
        shake     : true,
        showIcon  : false,
        type      : 'error'
      };

    })

  }

  ngOnInit(): void {

    this.getAppointmentCategoryList();

    this.appointmentSetupService.appointmentsetupentryrefreshcast.subscribe((res:any) => {
      if(res){
        this.getAppointmentCategoryList();
      }
    })

    this.appointments$.pipe( filter(res => res != null),
    ).subscribe((res:any) => {

      this.totalItems = res.length;
      this.ItemStartIndex = 0;

      if (this.totalItems > this.itemLimit) {
        this.ItemEndIndex = this.itemLimit;
      }
      else {
        this.ItemEndIndex = this.totalItems;
      }

    });

    // Subscribe to search input field value changes
    this.searchData.valueChanges
    .pipe(
      startWith(''),
      map((val) => {

        let newData = this.fullAppointmentsList.filter(item => {
          for (let field in item) {
            if (item[field] === null || item[field] === undefined) {
              continue;
            }
            if (item[field].toString().toLowerCase().indexOf(val.toString().toLowerCase()) !== -1) {
              return item;
            }
          }
        })

        this._appointmentsList.next(newData);


      })
    )
    .subscribe();

    this.deleteSubscribe = this.modalsService.deleteindexcast.subscribe((id:any) => {


      if(id != null) {

        this.fullAppointmentsList = this.fullAppointmentsList.filter(item => {
          return item.id != id
        })
        this._appointmentsList.next(this.fullAppointmentsList);

      }

    })

  }

  ngOnDestroy(): void
    {
        // Unsubscribe from all subscriptions
        this._unsubscribeAll.next();
        this._unsubscribeAll.complete();
        this.deleteSubscribe.unsubscribe();
    }

}
