import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
@Injectable({
  providedIn: 'root'
})
export class AppointmentSetupService {

  private appointmentSetupMatDrawer = new BehaviorSubject<any>(null);
  appointmentsetupdrawercast = this.appointmentSetupMatDrawer.asObservable();

  private appointmentSetupEntryRefresh = new BehaviorSubject<any>(false);
  appointmentsetupentryrefreshcast = this.appointmentSetupEntryRefresh.asObservable();

  constructor() { }

  getAppointmentSetupMatDrawer(){
    return this.appointmentSetupMatDrawer.value
  }

  setAppointmentSetupMatDrawer(value){
    this.appointmentSetupMatDrawer.next(value);
  }

  getAppointmentSetupEntryRefresh(){
    return this.appointmentSetupEntryRefresh.value
  }

  setAppointmentSetupEntryRefresh(value){
    this.appointmentSetupEntryRefresh.next(value);
  }

}
